@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
    font-family: 'Nunito', sans-serif;
}

.para {
    margin: none;
}

.align-left {
    text-align: left;
}

.align-right {
    text-align: right;
}

.center {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

.supercenter {
    position: relative;
    padding-right: auto;
    padding-left: auto;
    margin-right: auto;
    margin-left: auto;
}

.profile-button {
    font-weight:bold;
    margin: auto;
}

.profile-button:hover {
    background: linear-gradient(90deg, rgb(149, 250, 255), rgb(255, 182, 213) );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;
    font-weight: 1000;
  }

 .game-nav-button {
    text-align: center;
    place-items: center;
    margin-top: auto;
    margin-bottom: auto;
 }


button:focus{
    color:#c5b9d1;
    font-weight: 900;
    border: none;
    outline: none;
    box-shadow: none !important;
 }

button {
    border: none;
    width: 100%
}


 .header-button:hover {
    background: linear-gradient(90deg, rgb(149, 250, 255), rgb(255, 182, 213) );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;
    font-weight: 1000;
  }

  .nav-icon {
    display: grid;
    place-items: center;
    margin-right: none;
    margin-left: none;
    padding-right: auto;
    padding-left: auto;
    place-items: center;
    height: auto
  }

  .nav-element {
    max-width: 90px;
    max-height: 90px;
    margin-right: none;
    margin-left: none;
    padding-right: none;
    padding-left: none;
  }

  .no-margins {
      margin-left: none;
      margin-right: none;
      padding-right: none;
      padding-left: none;
  }

  .dot {
      margin-top: auto;
      margin-bottom: auto;
      width: 50px;
  }

#white-title {
    color: whitesmoke;
}

#userbox {
    background: linear-gradient(90deg, rgba(233, 254, 255) 20%, rgba(255, 233, 243) 100%);
    width: 100%;
    border-radius: 30px;
    border-color: rgb(55, 50, 70);
    border-width: 10px;
    border-style: solid;
    z-index: 1;
}

#display {
    width: 88%;
    color: rgb(185, 180, 206);
    background-color: rgba(52, 44, 61, 0.6);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin-top: -15px;
}

#display-nav {
    background-color:  rgb(55, 50, 70);
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

#profile-name {
    font-weight: 900;
    color: rgb(55, 50, 70);
}

#iconbox {
    background-color: rgba(254, 254, 255, 0.4);
    border-radius: 10px;
}

.taskbox {
    background-color: rgba(195, 186, 221, 0.2);
    border-radius: 10px;
}

.parent {
    display: grid;
    place-items: center;
    text-align:center
}

.page-header {
    background-color: rgb(35, 33, 43, 0.7);
    width:100%;
    text-align:center;
    border-color: rgb(45, 40, 58);
    border-width: 10px;
    border-top-style: solid;
}

.header-nav-color {
    font-size: 19px;
    height: 30px;
    text-align: center;
    color:#c5b9d1;
    font-weight: 900;
    background-color: none;
    border: none;
}

.nav-border {
    border-bottom-style: solid;
    border-color: rgb(45, 40, 58);
    border-width: 10px;
}

.title {
    size: 30px;
    font-weight: bold;
    background: linear-gradient(90deg, rgb(149, 250, 255), rgb(255, 182, 213) );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    z-index: 2
}

.title-button {
    size: 20px;
    font-weight: bold;
    color: rgb(17, 17, 20);
}

.row-width {
    width: 100%
}

@media screen and (min-width: 550px){   
    .icon-break {
        display: none;
    }
}

@media screen and (min-width: 1200px){   
    .dot {
        display:block
    }
}

@media screen and (max-width: 1050px){   
    #display-nav {
        width: 90%
    }
} 

@media screen and (max-width: 700px){   
    .header-nav-color {
        text-align: center
    }
} 

@media screen and (max-width: 550px){   
    .dot {
        display: none;
    }
}

